  --white: #fff;
  --light-brown: #8c7370;
  --brown: #594240;
}

body {
  background-color: var(--m-grey);
  max-width: auto;
  margin: 0;
  padding: 0;
}

.search-form {
  background-color: var(--grey);
  border: 1px solid var(--light-brown);
  justify-content: center;
  gap: 15px;
  margin-bottom: 20px;
  padding: 15px 0;
  display: flex;
}

input {
  padding: 10px 15px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  display: flex;
}

.photo-card {
  background-color: var(--light-grey);
  border: 1px solid var(--dark);
  width: 325px;
  height: 275px;
  box-shadow: 2px 2px 10px var(--dark);
  display: block;
}

.pictures {
  width: 325px;
